* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: "Josefin Sans", sans-serif;
}

header {
   width: 100%;
   height: 100vh;
   background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.15)),
      url("image1.jpg");
   background-repeat: no-repeat;
   background-size: cover;
}

nav {
   width: 100%;
   height: 15vh;
   color: white;
   display: flex;
   justify-content: center;
   align-items: center;
   text-transform: uppercase;
}

nav .logo {
   width: 35%;
   text-align: center;
   margin-top: 70px;
}

nav .logo h1 {
   font-size: 40px;
}

nav .logo h1 img {
   width: 100%;
   box-shadow: aqua 0px 5px 5px 0px;
}

main {
   width: 100%;
   height: 70vh;
   display: flex;
   justify-content: center;
   align-items: center;
   text-align: center;
   color: white;
}

section h3 {
   font-size: 3.4rem;
   font-weight: 200;
   letter-spacing: 3px;
   text-shadow: 1px 1px 2px black;
   margin-bottom: 50px;
}

section a {
   padding: 12px 30px;
   border-radius: 4px;
   outline: none;
   text-transform: uppercase;
   font-size: 1.5rem;
   font-weight: 500;
   text-decoration: none;
   letter-spacing: 1px;
   transition: all 0.5s ease;
   margin: 15px;
}

section .btnone {
   background: #00b894;
   color: white;
}

.btnone:hover {
   background: white;
   color: #00b894;
   box-shadow: aqua 0 0 30px 0;
}

section .btntwo {
   background: #00b894;
   color: white;
   ;
}

.btntwo:hover {
   background: #fff;
   color: black;
   box-shadow: aqua 0 0 30px 0;
}